import React, { useState } from "react";

function Body() {

    let [message, setMessage] = useState("Welcome");
    let [prompt, setPrompt] = useState("Start");

    let [animation, setAnimation] = useState();

    function handleClick() {
        
        console.log(prompt);
        if (prompt === "Start") {
            setMessage("Breathe In. Breathe Out.");
            setAnimation({border: "2px solid white"});
            setPrompt("Stop");
        } else {
            setMessage("Welcome");
            setAnimation({border: "none"})
            setPrompt("Start");
        }
    }

    

  return (
    <div className="body">
      <p>{message}</p>
      <br></br>
      <br></br>
      <br></br>
      <div className="circle" style={animation}>
      </div>
      <br></br>
      <button className="startButton" onClick={handleClick}>{prompt}</button>
    </div>
  );
}

export default Body;
